.header {
    width: 100%;
    display: flex;
    gap: 20px;
    align-items: center;
    justify-content: space-between;
    
    .logo img {
        width: 200px;
        height: auto;
    }
    ul {
        display: flex;
        width: 100%;
        justify-content: space-around;

        li {
            list-style-type: none;
            img {
                width: 30px;
                height: 30px;
            }
        }
    }
    
}
